    <h4 class="fw-bold">Korpus Polski wchodzi do gry</h4>
    <p>
      Uwolnieni z sowieckich więzień i łagrów Polacy wstępowali w szeregi polskiej armii, która stała się zaczątkiem
      największej jednostki Polskich Sił Zbrojnych na Zachodzie. Żołnierze pod dowództwem gen. Władysława Andersa
      wsławili się, zdobywając m.in Monte Cassino, Ankonę i Bolonię.
      „Pociąg zatrzymał się na stacji Buzułuku, wyjrzałem przez okno i ku swojemu zdumieniu zobaczyłem żołnierza w
