      Pancernego „Dzieci Lwowskich”. Pod koniec 1939 roku aresztowali go Sowieci i zesłali do Kazachstanu. Pracował w
      kopalni węgla w Karagandzie, skąd po napaści Niemiec na ZSRR w 1941 roku udało mu się uciec. Gdy został żołnierzem
      gen. Andersa, miał 19 lat.
    </p>
    <h4 class="fw-bold"> Od Syberii po Egipt</h4>
    <p>
      „2 Korpus Polski powstał na mocy rozkazu naczelnego wodza gen. Władysława Sikorskiego z 29 czerwca 1943 roku” –
      przypomina dr Janusz Kowalewski, historyk zajmujący się dziejami Polskich Sił Zbrojnych na Zachodzie. Utworzenie
      jednostki poprzedziło zawarcie 30 lipca 1941 roku porozumienia Sikorski-Majski, na mocy którego rozpoczęto w ZSRR
      formowanie Armii Polskiej na Wschodzie. W jej szeregi ściągali Polacy uwolnieni z łagrów i obozów sowieckich.
