      „Nieludzką ziemię” opuściło wówczas 116,5 tys. polskich obywateli, w tym ponad 78 tys. żołnierzy. Powstała w ZSRR
      Armia Polska stała się główną siłą utworzonego 21 lipca 1943 roku w Iraku 2 Korpusu, największej jednostki
      Polskich Sił Zbrojnych na Zachodzie. Na jej czele stanął generał Władysław Anders.
      W skład korpusu weszły 5 i 6 Dywizja Piechoty, z których sformowano 5 Kresową Dywizję Piechoty. Dołączono do nich
