      W sumie jesienią 1943 roku siły korpusu liczyły prawie 53 tys. żołnierzy, w tym ponad 3 tys. oficerów. „Jednostka
      została podporządkowana brytyjskiej 9 Armii, której dowództwo wyznaczyło termin osiągnięcia przez korpus gotowości
